import { Box, Flex, Center, Heading, Grid, Image, Text } from '@chakra-ui/react'
import '../CSS/Skills.css'
import { Context } from './ContextProvider'
import { useContext } from 'react'


const Skills = () => {
 let {isValue}=useContext(Context)
    return (
        <Flex  w='100%' m="auto" justify={'center'} className="main" bg={isValue==='dark'?'blackAlpha.900':'white'} id="skills">
            <Flex direction="column" w={{sm:'90%',md:'80%',lg:'80%'}} gap="100px" className="skills-card-name">              
                    <Heading textAlign={'center'} color={isValue==='dark'?'white':'blackAlpha.900'} pt="70px" fontSize="50px">
                       Skills
                    </Heading>  
                            
                <Grid templateColumns={{base:'repeat(2,1fr)',md:'repeat(3,1fr)',lg:'repeat(5,1fr)'}} gap="60px" pb='70px' >
                   {
                    data?.map((el,ind)=>(
                        <Flex key={ind} justify={'center'} alignItems={'center'} className="skills-card" id='card' direction={'column'} gap='10px'>
                            <Image color="orange" src={el.href} w="110px" h="110px" bg='black' borderRadius='10%' className='skills-card-img' />
                            <Text color={isValue==='dark'?'white':'blackAlpha.900'} fontWeight={'bolder'} fontSize={'18px'} className='skills-card-name'>{el.text}</Text>
                        </Flex>
                    ))
                   }
                </Grid>
            </Flex>

        </Flex>
    )
}
let data=[
    {   text:'Html',
        href:'https://upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/2048px-HTML5_logo_and_wordmark.svg.png'},
    {   text:'Css',
        href:'https://upload.wikimedia.org/wikipedia/commons/thumb/6/62/CSS3_logo.svg/800px-CSS3_logo.svg.png'},
    {   text:'JavaScript',
        href:'https://cdn.worldvectorlogo.com/logos/javascript-1.svg'},
    {   text:'React',
        href:'https://cdn.worldvectorlogo.com/logos/react-1.svg'},
    {   text:'Express',
        href:'https://adware-technologies.s3.amazonaws.com/uploads/technology/thumbnail/20/express-js.png'},
    {   text:'NodeJS',
        href:'https://www.nicepng.com/png/detail/80-803546_javascript-vector-transparent-node-js-logo-png.png'},
    {   text:'MongoDB',
        href:'https://1000logos.net/wp-content/uploads/2020/08/MongoDB-Emblem.jpg'},
    {   text:'Redux',
        href:'https://upload.wikimedia.org/wikipedia/commons/4/49/Redux.png'},
    {   text:'ChakraUI',
        href:'https://www.happylifecreators.com/wp/wp-content/uploads/2022/06/chakra-ui_title2-770x528.png'},
    {   text:'Python',
        href:'https://images.ctfassets.net/mrop88jh71hl/55rrbZfwMaURHZKAUc5oOW/9e5fe805eb03135b82e962e92169ce6d/python-programming-language.png'}
];
export default Skills;

