import { Box, Button, Center, Flex, Grid, Heading, Image, Link, Text } from '@chakra-ui/react'
import React, { useState, useContext } from 'react'
import '../CSS/Project.css'
import { Context } from '../Pages/ContextProvider'


const Project = () => {
  let { isValue } = useContext(Context)

  let [set, setClass] = useState('')

  let fun = () => {
    if (window.pageYOffset > 0) {
      setClass('div')

    } else {
      setClass('')

    }
  }
  window.addEventListener('scroll', fun)
  return (
    <Box
      bg={isValue === 'dark' ? 'blackAlpha.900' : 'white'}
      direction={'column'}
      gap='100px'
      id="projects" >
      <svg viewBox="0 50 500 100">
        <path d="M 0 50 C 150 150 330 -30 500 50 L 500 0 L 0 0" fill="black" opacity="0.8"></path>
        <path d="M 0 50 C 150 150 300 0 500 80 L 500 0 L 0 0" fill="black"></path>
        <path d="M -100 50 C 100 150 0 0 500 100 L 500 0 L 0 0" fill="black" opacity="0.5"></path>
      </svg>
      <Flex
        direction={'column'}
        gap="90px"
        mt={{ base: '', md: '-30px', lg: "-160px" }}
        pb="50px">
        <Center>
          <Heading
            color={isValue === 'dark' ? 'white' : 'blackAlpha.900'} fontSize="50px">
            Projects
          </Heading>
        </Center>
        <Center>
          <Grid
            templateColumns={{ base: 'repeat(1,1fr)', md: 'repeat(1,1fr)', lg: 'repeat(2,1fr)' }}
            gap="40px"
            w={{ base: '100%', md: '80%', lg: '80%' }}>
            {
              data.map((el, ind) => (

                <Flex key={ind}
                  p="30px"
                  direction={'column'}
                  className="project-card"
                  gap={'10px'}
                  id={set}
                  bg={isValue === 'dark' ? 'gray.300' : 'blackAlpha.900'}>
                  <Image src={el.image} />
                  <Center>
                    <Heading
                      color={isValue === 'dark' ? 'blackAlpha.900' : 'white'}
                      fontSize={'27px'}
                      className="project-title">
                      {el.title}
                    </Heading>
                  </Center>
                  <Center>
                    <Text
                      textAlign={'center'}
                      className="project-description"
                      color={isValue === 'dark' ? 'blackAlpha.900' : 'white'}>
                      {el.desc}
                    </Text>
                  </Center>
                  <Flex
                    pt="20px"
                    alignItems={'center'}
                    direction={{base:'column',md:'row',lg:'row'}}
                    gap="10px"
                    justify={'space-between'}>

                    <Flex direction={'column'}>
                      <Flex className='project-tech-stack'
                        flexWrap="nowrap"
                        direction={'row'}
                        gap="10px" >
                        {
                          el.tech.map(ele => (
                            <Text
                            bg={isValue==='dark'?'blackAlpha.900':'white'}
                              pl="4px" borderRadius="5px" pr="4px"
                              fontWeight={'bold'}
                              color={isValue === 'dark' ?  'white':'blackAlpha.900' }>
                              {ele}
                            </Text>
                          ))
                        }
                      </Flex>
                    </Flex>
                    <Flex gap="20px">
                      <Link
                        className="project-github-link"
                        href={el.git} target="_blank">
                        <Button
                          bg={isValue === 'light' ? 'white' : 'blackAlpha.900'}
                          color={isValue === 'light' ? 'blackAlpha.900' : 'white'}
                          px='20px'>
                          Git
                        </Button>
                      </Link>
                      <Link className="project-deployed-link"
                        href={el.online} target="_blank">
                        <Button
                          bg="red.600"
                          colorScheme={'white'}
                          px='20px'>
                          Live
                        </Button>
                      </Link>
                    </Flex>
                  </Flex>
                </Flex>

              ))
            }
          </Grid>
        </Center>
      </Flex>
      {/* <Image src='https://github-readme-streak-stats.herokuapp.com?user=ErGopiVishwakarma&theme=dark&border_radius=11&mode=weekly' w="100%" /> */}
    </Box>
  )
}

export default Project

