import { Box, Button, Center, Flex, Heading, Image, Link, Text } from '@chakra-ui/react'
import React, { useState } from 'react'
import '../CSS/About.css'
import logo from '../logo.png'
import { useContext } from 'react'
import { Context } from '../Pages/ContextProvider'

const About = () => {
  let { isValue } = useContext(Context)
  let [set, setClass] = useState('')
  let [set2, setClass2] = useState('')
  let fun = () => {

    if (window.pageYOffset > 0) {
      setClass('logo')
      setClass2('summary')
    } else {
      setClass('')
      setClass2('')
    }
  }
  window.addEventListener('scroll', fun)


  return (
    <Box id="about" bg={isValue === 'dark' ? "blackAlpha.900" : 'white'} className="about section" >
      <svg viewBox="0 50 500 100">
        <path d="M 0 50 C 150 150 330 -30 500 50 L 500 0 L 0 0" fill="black" opacity="0.8"></path>
        <path d="M 0 50 C 150 150 300 0 500 80 L 500 0 L 0 0" fill="black"></path>
        <path d="M -100 50 C 100 150 0 0 500 100 L 500 0 L 0 0" fill="black" opacity="0.5"></path>
      </svg>
      <Flex mt={{ base: '', md: '-30px', lg: "-160px" }} direction={'column'} gap="70px" pb='90px'>
        <Center>
          <Heading color={isValue === 'dark' ? 'white' : 'blackAlpha.900'} fontSize="50px">
            About Me
          </Heading>
        </Center>
        <Center>
          <Flex w={{ base: '100%', md: '80%', lg: '70%' }} alignItems={'center'} direction={{ base: 'column', md: 'column', lg: 'row' }} gap={{ base: '50px', md: '60px', lg: '70px' }}>

            <Flex className={set} id='logo' justify={{ base: 'center', md: 'center', lg: 'center' }} w={{ base: '90%', md: '80%', lg: '40%' }}>
              <Image src='https://raw.githubusercontent.com/harshmehta813/portfolio/9b25befc7b589966e62ac6353a562b58bd639c3d/src/Assets/about.png' borderRadius="50%"
              />
            </Flex>


            {/* Hello! My name is Gopi Vishwakarma and I am a Full Stack Web Developer, Student at <b style={{ color: 'blue' }}>Masai School</b>. Currently, I am interested in <b style={{ color: 'blue' }}>Front-End, Back-End</b>, and creating APIs, Working with databases, and working with React Mern Stack. I am extremely interested in <b style={{ color: 'blue' }}>Back-End Development. */}

            <Flex w={{ base: '90%', md: '80%', lg: '60%' }} className={set2} id="summary" fontWeight='bolder' direction="column" gap="10px">
              <Text textAlign={{ base: 'center', md: 'cetner', lg: 'start' }} fontStyle={'italic'} fontSize={'20px'} color={isValue === 'dark' ? 'white' : 'blackAlpha.900'} id="user-detail-intro">    Passionate Full Stack Web Developer with the ability
                to write efficient code using JavaScript. Problem
                solver and curious professional with proficiency in
                various front-end and back-end skills like ReactJS,
                NodeJS, ExpressJS, MongoDB etc. Looking forward
                to a challenging work environment.</Text>
              <Flex
                justify={{ base: 'center', md: 'cetner', lg: 'center' }}>
                <Link onClick={() => window.open('https://drive.google.com/file/d/1G3LDPbi-J5cLK2-2urdqK6Q7XNd7bsJ9/view')} style={{ textDecoration: 'none' }} href="GopiVishwakarma.pdf" target="_blank" download color="white" id="resume-button-2">
                  <Button
                    className="button" bg="red.500"
                    colorScheme="white"
                    textStyle={'none'}
                  >
                    Resume
                  </Button>
                </Link>
              </Flex>
            </Flex>
          </Flex>
        </Center>
      </Flex>

    </Box>
  )
}

export default About