import {
  Box, Flex, Button, Text, IconButton, Stack, Image, Collapse, Icon, useColorModeValue, useBreakpointValue, useDisclosure, Link
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon, ChevronDownIcon, ChevronRightIcon, MoonIcon, SunIcon } from '@chakra-ui/icons';
import { NavHashLink } from 'react-router-hash-link';
import '../CSS/Navbar.css'
import { useContext,useState } from 'react'
import { Context } from '../Pages/ContextProvider'

let count = 0
let value = ''
let darkFun
export const Navbar = () => {
  let { isValue, darkModeFun } = useContext(Context)
  let [set,setClass]=useState('')
  darkFun = darkModeFun
  value = isValue
  const { isOpen, onToggle } = useDisclosure();

  window.addEventListener('wheel',(e)=>{
    if(e.deltaY<0){
      setClass('')
    } else {
      setClass('nav')
    }
  
  })

  return (
    <Box bg="blackAlpha.900" position="fixed" zIndex="999"  w="100%" h="70px" className={set} id="nav-menu" boxSizing="border-box">
      <Flex bg={useColorModeValue()} color={useColorModeValue()} minH={'60px'} py={{ base: 2 }} px={{ base: 4 }} alignItems={'center'}>

        <Flex flex={{ base: 1 }} justify={{ base: 'left', md: 'space-between' }}>
          <NavHashLink smooth to='#home' className='nav-link home'>
            <Text
              textAlign={useBreakpointValue({ base: 'left', md: 'left' })}
              fontSize={{ base: '20px', md: '25px' }}
              fontStyle="italic"
              color={useColorModeValue('white', 'white')}>
              Gopi vishwakarma
            </Text>
          </NavHashLink>
          <Flex display={{ base: 'none', md: 'none',lg:'block' }} >
            <DesktopNav />
          </Flex>
        </Flex>
        <Flex flex={{ base: 1, md: 'auto' }} justify={{ base: 'right' }} display={{ base: 'flex', md: 'flex',lg:'none' }}>
          <IconButton
            onClick={onToggle}
            icon={
              isOpen ? <CloseIcon w={5} h={5} color="white" /> : <HamburgerIcon w={7} h={7} color="white" />
            }
            variant={'ghost'}
            aria-label={'Toggle Navigation'}
          />
        </Flex>


      </Flex>
      <Collapse in={isOpen} animateOpacity>
        <MobileNav />
      </Collapse>
    </Box>
  );
}

const DesktopNav = () => {
  const linkColor = useColorModeValue('white', 'white');
  const linkHoverColor = useColorModeValue('white', 'white');

  return (
    <Flex direction={'row'} gap={10} alignItems="center" fontStyle="italic" className="nav">



      {NAV_ITEMS.map((navItem, ind) => (
        <Box key={++count} color="white" className='navChild'>
          <NavHashLink
            smooth
            to={navItem.href}
            className={navItem.clss}
          >
            {navItem.label}

          </NavHashLink>
        </Box>
      ))}
      <p className='nav-link resume'><Link onClick={()=>window.open('https://drive.google.com/file/d/1G3LDPbi-J5cLK2-2urdqK6Q7XNd7bsJ9/view')} style={{textDecoration:'none'}} href="GopiVishwakarma.pdf" target="_blank" download color="white"  id="resume-button-1 resume-link-1" >
        Resume
      </Link></p>
      <Button onClick={() => darkFun()} >
        {value === 'light' ? <MoonIcon /> : <SunIcon />}
      </Button>
    </Flex>
  );
};



const MobileNav = () => {
  return (
    <Stack
      bg={useColorModeValue('blackAlpha.800', 'gray.800')}
      p={10}
      display={{ md: 'block' ,lg:'none'}}
      h="100vh"
      zIndex='999'>
      {NAV_ITEMS.map((navItem) => (
        <MobileNavItem key={navItem.label} {...navItem} />
      ))}
      <Flex
        py={4}
        onClick={darkFun}
        justify={'center'}
        align={'center'}
        _hover={{
          textDecoration: 'none',
          backgroundColor:'pink',
          color:'blackAlpha.900'
        }}>
        <Text
          fontWeight={'bolder'}
          fontSize="18px"
          color={useColorModeValue('white', 'white')}>
          {value==='dark'?'Light mode':'Dark mode'}
        </Text>
      </Flex>
    </Stack>
  );
};

const MobileNavItem = ({ label, children, href }) => {
  const { isOpen, onToggle } = useDisclosure();

  return (
   
      <Flex
        py={4}
        as={Link}
        href={href ?? '#'}
        justify={'center'}
        align={'center'}
        fontSize="18px"
        _hover={{
          textDecoration: 'none',
          backgroundColor:'pink',
          color:'blackAlpha.900'
        }}>
        <Text
          fontWeight={'bolder'}
          color={useColorModeValue('white', 'white')}>
          {label}
        </Text>
      </Flex>
  );
};
export default Navbar

const NAV_ITEMS = [
  {
    label: 'About',
    href: '#about',
    clss:'nav-link about'
  },
  {
    label: 'Skills',
    href: '#skills',
     clss:'nav-link skills'
  },
  {
    label: 'Project',
    href: '#projects',
     clss:'nav-link projects'
  },
  {
    label: 'Contact',
    href: '#contact',
     clss:'nav-link contact'
  }
];
